import urlParam from './decode-url';

// config
const config = {
    'show_fabrication': false,
    'redraw_on_drag': true,
    'highlight_pin1': false,
    'extra_fields': [],
    'dark_mode': true,
    'bom_view': 'left-right',
    'board_rotation': 0.0,
    'checkboxes': 'Sourced,Placed',
    'show_silkscreen': true,
    'show_pads': true,
    'layer_view': 'FB'
}

// pcbdata
import aod_deflect_data from '../data/aod-deflect.json';
import car_data from '../data/car.json';
import dvd_control_data from '../data/dvd-control.json';
import peltier_cooler_data from '../data/peltier_cooler.json';
import phase_lock_data from '../data/phase_lock.json';
import pulse_oximeter_data from '../data/pulse_oximeter.json';

function getJSON(tag) {
    switch (tag) {
        case 'car':
            return car_data;
        case 'dvd-control':
            return dvd_control_data;
        case 'phase_lock':
            return phase_lock_data;
        case 'pulse_oximeter':
            return pulse_oximeter_data;
        case 'aod-deflect':
            return aod_deflect_data;
        default:
            return peltier_cooler_data;
    }
}

const pcbdata = getJSON(urlParam('pcbdata'));

export {config, pcbdata};
